import React, { ReactNode, CSSProperties, PropsWithChildren } from 'react';
import styled from 'styled-components';
import { CalendarCardMetadataType } from '../type';

export interface BaseParagraphProps {
  metadata?: CalendarCardMetadataType;
  innerWidth?: string | number;
};

const ParagraphWithIconWrapper = styled.div`
  display: flex;
  flex-direction: row;
`;

const IconWrapper = styled.div`
  height: 20px;
  padding-right: 8px;
  display: flex;
  align-items: center;
`;
export const IconInnerWrapper = styled.div`
  padding-left: 3px;
  display: inline-flex;
  align-items: center;
`;
const TextWrapper = styled.div`
  width: calc(100% - 26px);
`;

export default function paragraphFactory(icon?: ReactNode | string, style?: CSSProperties) {
  return (props: PropsWithChildren<any>) => {
    const { children } = props;
    return (
      <ParagraphWithIconWrapper style={style}>
        <IconWrapper>
          <IconInnerWrapper>{icon}</IconInnerWrapper>
        </IconWrapper>
        <TextWrapper>{children}</TextWrapper>
      </ParagraphWithIconWrapper>
    );
  };
}
